<template>
  <astro-section header="ガイド紹介">
    <div v-for="guide in guides">
      <div class="col-md-4">
        <div class="panel panel-default">
          <div class="panel-body">
            <h4>{{guide.name}}</h4>
          </div>
          <img :src="require(`assets/images/guides/${guide.fileName}`)" />
          <div class="panel-footer">
            <p>{{guide.text}}</p>
          </div>
        </div>
      </div>
    </div>
  </astro-section>
</template>

<script>
  import astroSection from '../organisms/Section.vue'
  import guides from 'assets/jsons/guides.json'

  export default {
    name: 'Guides',
    data: () => guides,
    components: { astroSection },
  }
</script>



