<template>
  <nav class="navbar navbar-default">
    <div class="container">
      <div class="collapse navbar-collapse">
        <ul class="nav navbar-nav">
          <li v-for="item in navItems">
            <a href="#">{{ item.text }}</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
  export default {
    name: 'Navbar',
    data: () => ({
      navItems: [
        { key: 'top', text: 'TOP' },
        { key: 'price', text: '料金' },
        { key: 'guides', text: 'ガイド紹介' },
        { key: 'photos', text: '写真' },
        { key: 'wether', text: '天気' },
        { key: 'contact', text: 'お問い合わせ' },
      ],
    }),
  }
</script>



